import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {CarrinhoService} from '../../service/service_carrinho/carrinho.service';
import {Observable, of, throwError} from 'rxjs';
import {catchError, map, tap} from 'rxjs/operators';
import {Carrinho} from '../../models/carrinho';
import {CadastroService} from '../../service/service_login/cadastro.service';
import {Usuario} from '../../models/usuario';
import {AuthService} from '../../service/service_login/auth.service';

@Component({
  selector: 'app-comprar',
  templateUrl: './comprar.component.html',
  styleUrls: ['./comprar.component.scss'],
})
export class ComprarComponent implements OnInit {
  produto: Carrinho[] = [];
  produtos$: Observable<Carrinho[]>;
  total = 0;
  id: number;

  constructor(private route: Router, private carrinhoService: CarrinhoService, private authService: AuthService) {
  }

  ngOnInit(): void {
    this.getCarrinho();
    this.id = Number(this.authService.getUserId().id);
  }

  getCarrinho() {
    this.produtos$ = this.carrinhoService.getCarrinho()
      .pipe(
        tap(produto => {
          this.produto = produto;
          this.calculaTotal();
        }),
        catchError(error => {
          return throwError(error.message);
        })
      );
  }

  calculaTotal() {
    this.total = this.produto.reduce((prev, curr) => prev + (curr.price), 0);
  }

  removeProdutoCarrinho(id: number) {
    this.carrinhoService.deleteCarrinho(id).pipe(
      tap(
        res => {
          this.produto = this.produto.filter(produto => produto.id !== res);
          this.produtos$ = of(this.produto);
          alert('removido');
          this.calculaTotal();
        }
      )
    ).subscribe();
  }

  comprar() {
    this.carrinhoService.comprarCarrinho().subscribe(res => {
      alert('parabéns, você finalizou a sua compra!');
      this.getCarrinho();
    });
  }
}
